<template>
  <div style="width: 100%; position: relative; border-right: 1px solid #ccc; height: 100%;">

    <v-btn @click="capNhatThongTin()" class="px-0 mx-0 py-0 mt-0 mb-1" color="primary" style="width: 100%;">Cập nhật thông tin</v-btn>

    <div class="expansion-panel__header" data-ripple="false" style="background-color: #14bef0; color: #fff;     padding: 9px 9px 8px 9px;"><div>CHI TIẾT DOANH NGHIỆP</div>
      <div class="header__icon"></div>
    </div>
		<div class="flex xs12">
      <v-list>
        <v-list-tile
          :class='{"list--group__header--active": (activeIndex === "ThongTinChung")}'
          :key="'thong-tin-chung'"
          avatar
          @click="toThongTinChung()"
          >
          <v-list-tile-avatar>
            <i v-if="activeIndex === 'ThongTinChung'" style="font-size: 18px;" class="material-icons">
              play_arrow
            </i>
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title style="font-weight: 600">Thông tin chung</v-list-tile-title>
          </v-list-tile-content>

        </v-list-tile>

        <v-list-tile
          :disabled="!thongTinChung['registrationId']"
          :style="{'pointer-events': !thongTinChung['registrationId'] ? 'none' : ''}"
          :class='{"list--group__header--active": (activeIndex === "ThongTinXuong")}'
          :key="'thong-tin-xuong'"
          avatar
          @click="toThongTinXuong()"
          >
          <v-list-tile-avatar>
            <i v-if="activeIndex === 'ThongTinXuong'" style="font-size: 18px;" class="material-icons">
              play_arrow
            </i>
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title style="font-weight: 600">Thông tin xưởng</v-list-tile-title>
          </v-list-tile-content>

        </v-list-tile>
      </v-list>
    </div>
    <div class="expansion-panel__header" data-ripple="false" style="background-color: #14bef0; color: #fff;     padding: 9px 9px 8px 9px;"><div>LỊCH SỬ CẬP NHẬT</div>
      <div class="header__icon"></div>
    </div>
    <div style="width: 100%;" class="ml-3" v-if="loading">
      <content-placeholders>
        <content-placeholders-text :lines="4" />
      </content-placeholders>
    </div>
    <v-list two-line v-else>
      <v-list-tile
        v-for="(item, index) in historyUpdates"
        :class='{"list--group__header--active": (activeIndex === item.registrationId)}'
        :key="item['modifiedDate']"
        avatar
        @click="toDetailThongTinChung(item, index)"
        >
        <v-list-tile-avatar>
          <i class="fa fa-book fs20" aria-hidden="true"></i>
        </v-list-tile-avatar>

        <v-list-tile-content>
          <v-list-tile-title style="font-weight: 600">{{item['modifiedDate'] | registrationState}}</v-list-tile-title>
          <v-list-tile-sub-title>{{item['modifiedDate'] | parseDate}}</v-list-tile-sub-title>
        </v-list-tile-content>

      </v-list-tile>
    </v-list>
  </div>
</template>
<script>
// import axios from 'axios'
// import router from '@/router'
const COMPONENT_NAME = 'jx-hanghai-filter-left'
import toastr from 'toastr'
toastr.options = {
  'closeButton': true,
  'timeOut': '3000'
}
export default {
  name: COMPONENT_NAME,
  props: {
  },
  components: {
  },
  data () {
    return {
      STATE_LUU_NHAP: 0,
      STATE_CHO_DUYET: 1,
      STATE_DA_DUYET: 2,
      STATE_CHO_BO_SUNG: 3,
      indexSelect: -1,
      loading: false,
      initDataFilter: {},
      activeIndex: 0,
      historyUpdates: [],
      dataFilter: []
    }
  },
  computed: {
    thongTinChung () {
      /* eslint-disable */
      let item = this.$store.getters.thongTinChung || {}
      console.log('return item', item)
      return item
      /* eslint-disable */
    }
  },
  created () {
    var vm = this
    let path = vm.$router.history.current.path
    console.log('patch,', path)
    if (path && path.indexOf('thong-tin-xuong') !== -1) {
      vm.activeIndex = 'ThongTinXuong'
    } else {
      vm.activeIndex = 'ThongTinChung'
    }
    vm.$nextTick(function () {
      vm.loading = true
      vm.$store.dispatch('loadInitResource').then(function (result) {
        vm.loading = false
        vm.loadHistoryUpdate({
          sort: 'modified'
        })
      }).catch(xhr => {
        vm.loading = false
      })
    })
  },
  watch: {
    '$route': function (newRoute, oldRoute) {
      // let vm = this
      // let query = newRoute.query
    }
  },
  methods: {
    capNhatThongTin () {
      let vm = this
      vm.$store.commit('setThongTinChung', {})
      vm.$router.push({
        path: '/thong-tin-chung/' + 0
      })
    },
    toDetailThongTinChung (item, index) {
      let vm = this
      vm.activeIndex = item['registrationId']
      vm.$store.commit('setThongTinChung', item)
      vm.$router.push({
        path: '/thong-tin-chung/' + item['registrationId']
      })
    },
    toThongTinChung () {
      let vm = this
      vm.activeIndex = 'ThongTinChung'
      if (!vm.loading && vm.historyUpdates.length) {
        let registration = vm.historyUpdates[0]
        vm.$store.commit('setThongTinChung', registration)
        vm.$router.push({
          path: '/thong-tin-chung/' + registration['registrationId']
        })
      } else {
        vm.$router.push({
          path: '/thong-tin-chung/0'
        })
      }
    },
    toThongTinXuong () {
      let vm = this
      vm.activeIndex = 'ThongTinXuong'
      let registration = vm.historyUpdates[0]
      vm.$store.commit('setThongTinChung', registration)
      vm.$router.push({
        path: '/thong-tin-xuong'
      })
    },
    loadHistoryUpdate (params) {
      let vm = this
      vm.loading = true
      vm.$store.dispatch('loadHistoryUpdate', params).then(result => {
        vm.historyUpdates = result['data'] || []
        let path = vm.$router.history.current.path
        if (path && path.indexOf('thong-tin-chung') !== -1 || path === '/') {
          if (!vm.historyUpdates.length) {
            vm.$router.push({
              path: '/thong-tin-chung/0'
            })
          } else {
            let registration = vm.historyUpdates[0]
            vm.$store.commit('setThongTinChung', registration)
            vm.$router.push({
              path: '/thong-tin-chung/' + registration['registrationId']
            })
          }
        }
        vm.loading = false
      }).catch(xhr => {
        vm.loading = false
        let path = vm.$router.history.current.path
        if (path && path.indexOf('thong-tin-chung') !== -1) {
          vm.$router.push({
            path: '/thong-tin-chung/0'
          })
        }
      })
    }
  },
  filters: {
    parseDate (date) {
      if (!date) {
        return ''
      }
      return moment(String(date)).utc().format('DD/MM/YYYY HH:mm:ss')
    },
    registrationState (state) {
      let vm = this
      if (state === 0) {
        return 'Lưu nháp'
      } else if (state === 1) {
        return 'Chờ duyệt'
      } else if (state === 2) {
        return 'Đã duyệt'
      } else {
        return 'Yêu cầu bổ sung'
      }
    }
  }
}
</script>



